import React from 'react';

const styles = {
  view: {
    borderTop: '2px solid rgba(0, 0, 0, 0.2)',
    marginLeft: '15px',
    marginRight: '15px',
  },
};

const UISeparator = () => (
  <div style={{ ...styles.view }} />
);

export default UISeparator;



